/* loading spinner */

.lds-ripple {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
  }
  .lds-ripple div {
    position: absolute;
    border: 4px solid rgb(240, 9, 97);
    opacity: 1;
    animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  }
  .lds-ripple div:nth-child(2) {
    animation-delay: -0.5s;
  }
  @keyframes lds-ripple {
    0% {
      top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
    transform: rotate(0deg);
    }
    33% {
      top: 28px;
      left: 24px;
      width: 16px;
      height: 16px;
      opacity: 1;
      transform: rotate(120deg);
    }
    66% {
      top: 28px;
      left: 44px;
      width: 16px;
      height: 16px;
      opacity: 1;
      transform: rotate(240deg);
    }
    100% {
      top: 28px;
      left: 4px;
      width: 16px;
      height: 16px;
      opacity: 1;
      transform: rotate(360deg);
    }
  }
  
  .lds-ripple div {
    border-radius: 0;
  transition: width 0.3s ease, height 0.3s ease, opacity 0.3s ease;
  }

  .lds-ripple div:before {
    content: "";
    display: block;
    width: 100%;
    height: 100%;
    background-color: rgb(240, 9, 97);
  }
  /* overlay to prevent clicks */
  
  .overlay {
    background-color: rgba(1, 1, 1, 0.7);
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 10;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  